import React, { SFC } from 'react';

export interface ButtonProps {
  appearance?: string;
  disabled?: boolean;
  children?: JSX.Element;
  onClick?: () => void;
}

const Button : SFC<ButtonProps> = ({
  appearance = 'default',
  disabled = false,
  children = null,
  onClick,
}) => (
  <button disabled={ disabled } onClick={ onClick }>
    { children }
  </button>
);

export default Button;
